import { useEffect, useState } from "react";
import { useRouter } from "next/router";
import { getAccessToken } from "@/utils/tokenUtils";
const withAuthAccess = (WrappedComponent: React.ComponentType) => {
  const AuthComponent = (props: any) => {
    const router = useRouter();
    const [accessToken, setAccessToken] = useState<string | null>(null);
    const [isLoading, setIsLoading] = useState(true);
    useEffect(() => {
      if (typeof window === "undefined") return;
      const accessToken = getAccessToken();
      if (accessToken) {
        if (router.pathname === "/login" || router.pathname === "/signup") {
          router.push("/");
          return;
        }
        setAccessToken(accessToken);
      } else {
        if (router.pathname !== "/login" && router.pathname !== "/signup") {
          router.push("/login");
        }
      }
      setIsLoading(false);
    }, [router]);
    if (isLoading) {
      return <div>Loading...</div>;
    }
    if (!accessToken && !(router.pathname === "/login" || router.pathname === "/signup")) {
      return null;
    }
    return <WrappedComponent {...props} data-sentry-element="WrappedComponent" data-sentry-component="AuthComponent" data-sentry-source-file="withAuthAccess.tsx" />;
  };
  return AuthComponent;
};
export default withAuthAccess;